.place {
  &__photo-container {
    position: relative;
    width: 100%;
    box-sizing: border-box;
    max-height: 256px;
    overflow: hidden;

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;

      &:hover {
        transform: scale(100%, 110%);
      }
    }

    transition: transform 1s;

    &:hover {
      transform: scale(105%, 110%);
      z-index: 1;
    }
  }

  &__photo {
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    color: $color-blue;
    text-decoration: none;
    font-weight: bold;
  }
}
